
import axios from "axios";
import router from "@/router";
import cheerio from "cheerio";
export default {
  created() {
    this.getAllCatagory();
    this.getallTags();
    this.getallPost();
    this.profile = JSON.parse(localStorage.getItem("profile") || "");
  },
  data: (): {
    catagories: any;
    txt_search: any;
    tagsList: any[];
    postList: any[];
    catagoryList: any[];
    toggle :boolean;
    profile : any[]
  } => ({
    catagories: null,
    txt_search: "",
    tagsList: [],
    postList: [],
    catagoryList: [],
    toggle : false,
    profile : []
  }),
  computed: {
    filteredList() {
      if (this.txt_search) {
        return this.tagsList.filter((tagList: { name: string }) => {
          return tagList.name
            .toLowerCase()
            .includes(this.txt_search.toLowerCase());
        });
      } else {
        return null;
      }
    },
    filteredListContent() {
      if (this.txt_search) {
        return this.postList.filter((postList: { content: string }) => {
          return postList.content
            .toLowerCase()
            .includes(this.txt_search.toLowerCase());
        });
      } else {
        return this.postList.slice(0,5);
      }
    },
    filteredListCat() {
      if (this.txt_search) {
        return this.catagoryList.filter((catList: { name: string }) => {
          return catList.name
            .toLowerCase()
            .includes(this.txt_search.toLowerCase());
        });
      } else {
        return null;
      }
    },
  },
  methods: {
    getAllCatagory() {
      let apiUrl = "https://interm-api.onrender.com/api/catagory/getall";
      axios
        .get(apiUrl)
        .then((res) => {
          this.catagories = res.data;
          this.catagoryList = res.data;
        })
        .catch((err) => {
          console.log(err.response.data.message);
        });
    },
    isLoggedOut() {
      localStorage.removeItem("access_token");
      localStorage.removeItem("profile");
      return router.push({ path: "/login" });
    },
    async getallTags() {
      let apiUrl = "https://interm-api.onrender.com/api/tags/getall";
      await axios
        .get(apiUrl, {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("access_token"),
          },
        })
        .then((res) => {
          this.tagsList = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    async getallPost() {
      let apiUrl = "https://interm-api.onrender.com/api/post/getall";
      await axios
        .get(apiUrl)
        .then((res) => {
          this.postList = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    extractH1(htmlString: string) {
      const $ = cheerio.load(htmlString);

      const h1 = $("h1").text();
      if (h1 != "") {
        return h1;
      } else {
        return "Unknown Title";
      }
    },
  },
};
